<template>
  <div class="main">

    <!-- 二级路由插座 -->
    <!-- 路由模块 -->
    <!-- 过渡效果与路由器结合 -->
		<transition mode="out-in" :duration="{ enter: 500, leave: 500 }" enter-active-class="animated fadeIn"  leave-active-class="animated fadeOut">
            <router-view></router-view>
		</transition>

    <!-- 底部导航 -->
    <van-tabbar v-model="tabbarName">
      <van-tabbar-item :name="item.name" :icon="item.icon" v-for="(item, index) in tabbarData" :key="index" @click.native="goPage(item)">{{item.text}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  export default {
    name: 'Main',

    data() {
      return {
        //底部导航索引
        tabbarName: 'menu'
      };
    },

    computed: {

      tabbarData() {
        return this.$store.state.mainModule.tabbarData;
      }
    },

    methods: {
      //跳转
      goPage(item) {

        this.$router.push({name: item.routeName});
      }
    }
  }
</script>

<style lang="less" scoped>

</style>